class Parent {
    constructor(name) {
        this.name = name;
    }
    doSomething() {
        console.log('parent do something!');
    }
    sayName() {
        console.log('parent name:', this.name);
    }
}

class Child extends Parent {
    constructor(name, parentName) {
        super(parentName);
        this.name = name;
    }
    set name(name) {
        this.name = name;
    }
    get name() {
        return this.name;
    }
    sayName() {
        console.log('child name:', this.name);
    }
}

new Child('child', 'parent');